/* Стили для хедера */
.flex_container {
  display: flex;
  justify-content: center;
  background-color: #845896; /* Фоновый цвет хедера */
  padding: 20px; /* Внутренние отступы */
}

nav {
  border: 1px solid #878787;
  background-color: #f5f5f5;
}

nav .logo-website-name {
  display: flex;
  justify-content: space-between;
}
nav .logo {
  display: flex;
  height: 50px;
  width: 40px;
  align-items: center;
  text-align: center;
  background-color: #731173;
  justify-content: center;
}
nav .logo {
  color: #f5f5f5;
  font-family: "Times New Roman", Times, serif;
  font-size: 32px;
  font-weight: 100;
}
nav .header-name {
  color: #731173;
  align-self: center;
  font-size: 16px;
  margin-left: 30px;
  font-weight: 400;
}
nav button {
  background-color: #731173;
  color: #f5f5f5;
  font-size: 16px;
  padding: 0 15px;
  height: 35px;
  border: none;
  border-radius: 5px;
  align-self: center;
  font-weight: 100;
}


.flex_logo {
  margin-right: 20px;
}

.text_container {
  flex-grow: 1;
  text-align: center;
  font-size: 24px; /* Размер текста */
  color: #fff; /* Цвет текста */
}

.flex_button {
  margin-left: 20px;
}

.text_button {
  cursor: pointer;
  padding: 10px 20px;
  background-color: #8d2fa0; /* Цвет кнопки */
  color: #fff; /* Цвет текста кнопки */
  border-radius: 5px;
}

/* Стили для заголовка */
.flex_heading {
  margin: 30px;
  
  display: flex;
  
  
  padding: 20px 0;
  
  border-radius: 20px;
}

.flex_heading h1 {
  font-size: 36px; /* Размер шрифта заголовка */
  color: rgb(255, 255, 255); /* Цвет заголовка */
  background-color: #731173; /* Фоновый цвет надписи */
  width: auto;
  margin: 0 auto; /* Центрирование по оси X */
  padding:20px;
  border-radius: 30px;
}




/* Стили для модального окна */
.modal-dialog {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.modal-content {
  text-align: center;
}

.modal-body {
  margin: auto;
}

/* Стили для контейнера карточек */
.card-container {
  margin-bottom: 2000px;
}

/* Стили для футера */
footer {
  display: flex;
  justify-content: space-between;
  border: 1px solid #878787;
  padding: 20px 0;
  align-items: center;
  margin-top: auto;
}
footer .container {
  display: flex;
  justify-content: space-between;
}
footer .about {
  font-weight: bold;
}
footer .links {
  display: flex;
  gap: 10px;
}
footer img {
  width: 25px;
  height: auto;
  padding: 5px;
}

.full-name {
  font-family: Arial, sans-serif;
  font-size: 25px;
  font-weight: bold;
  color: #fff;
  margin-right: 10px;
}

.flex_footer img {
  width: 50px;
  height: 50px;
  margin-left: 10px;
  cursor: pointer;
}

.flex_footer img:hover {
  opacity: 0.4;
}

/* Дополнительные стили */
.flex_objects_element {
  flex-direction: column;
  word-wrap: normal;
  width: 300px;
  margin-left: 50px;
  margin-right: 50px;
  margin-bottom: 20px;
}

.flex_logo {
  width: 50px;
  height: 70px;
  margin-top: 15px;
  margin-left: 100px;
}

.flex_button {
  display: flex;
  justify-self: center;
  margin-left: auto;
  margin-right: auto;
  background-color: #f629db;
  width: 100px;
  height: 30px;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
  margin-right: 50px;
}

.flex_button :hover {
  cursor: pointer;
}

.flex_image {
  width: 90px;
  height: 20px;
  background-color: #b3e5fc;
  margin-top: 10px;
  margin-left: 10px;
  font-size: 16px;
}

.text_logo {
  color: white;
  text-align: center;
  font-family: "Times New Roman", Times, serif;
}

.text_container {
  font-size: 40px;
  margin: auto;
  color: #ffffff;
}

.text_button {
  color: white;
  margin: auto;
  font-size: 16px;
}

.text_object_name {
  font-weight: bold;
  font-size: 24px;
}

.text_object_info {
  font-size: 16px;
}

.footer_images {
  display: flex;
  width: 50px;
  height: 50px;
  margin-top: auto;
  margin-bottom: auto;
  margin-right: 420px;
  margin-left: auto;
  align-content: center;
}

.toast {
  position: absolute;
  top: 10px;
  right: 10px;
}
